import './style.scss';
import AOS from 'aos';
import Swiper from 'swiper';
/* import { validate } from './ts/validate'; */
var viewportWidth = window.innerWidth;
var offset = viewportWidth * 0.01 / 14.4 * 582;
var offset2 = viewportWidth * 0.01 / 16.2 * 592;
const swiper_1 = new Swiper('#relax_slider', {
	slidesPerView: "auto",     
	slidesPerGroup:1,  
	freeMode:false,
	slidesOffsetBefore:582,
    spaceBetween: 20,
	breakpoints: {
	1460:{
		slidesOffsetBefore:offset,
	},
	1450:{
		slidesOffsetBefore:582,
	},
	1150:{
		slidesOffsetBefore:offset2,
	},
	900:{
		slidesOffsetBefore:306,
	},
	569:{
		slidesOffsetBefore:30,
	},
	321:{
		slidesOffsetBefore:15,
	}
}

  });
  swiper_1.on('slideChange', function () {
	if (swiper_1.activeIndex === 2) {
		swiper_1.allowSlideNext = false;
	} else {
		swiper_1.allowSlideNext = true;
	}
  });
   
   
const swiperContainer = document.querySelector('.entertainment-section1') as HTMLElement;

swiperContainer?.addEventListener('mouseover', function() {
  swiperContainer.style.cursor = 'url(./assets/move.png), auto';
});

swiperContainer?.addEventListener('mouseout', function() {
  swiperContainer.style.cursor = 'auto';
});
//swiper 2
new Swiper('#journey_slider', {
	slidesPerView: "auto",     
	speed:800,
	freeMode:false,
	loop:true,
	slideToClickedSlide:true,
    spaceBetween: 10,
	 

  });
  
const journeyInfo = document.querySelector('.journey-info-container-wrapper') as HTMLElement;

journeyInfo?.addEventListener('mouseover', function() {
	journeyInfo.style.cursor = 'url(./assets/move.png), auto';
});

journeyInfo?.addEventListener('mouseout', function() {
	journeyInfo.style.cursor = 'auto';
});

const cards = document.querySelectorAll('.boat-card');

cards.forEach(card => {
  card.addEventListener('click', () => {
    cards.forEach(item => item.classList.remove('main_box_active'));
    card.classList.add('main_box_active');
	card.scrollIntoView({ behavior: "smooth", block: "start" });
  });
});

document.getElementById('ajax-contact-form')?.addEventListener('submit', function(evt){
	evt.preventDefault();
	var http = new XMLHttpRequest(), f = this as HTMLFormElement;
	var th = $(this);
	console.log(th.serialize(), checkboxState)
	
	document.getElementById('ajax-contact-form')!.style.display = 'none';
	let mail_answer = document.querySelector('.mail_answer')!;
	let mail_answer_title = document.querySelector('.mail_answer_title')!;
	let mail_answer_text = document.querySelector('.mail_answer_text')!;

	mail_answer.classList.add('mail_answer_active');
	mail_answer_title.textContent = "Подождите";
	mail_answer_text.textContent = "Идет загрузка...";
	http.open("POST", "mail.php", true);
	http.onreadystatechange = function() {
	  if (http.readyState == 4 && http.status == 200) {


		mail_answer_title.textContent = "Спасибо";
		mail_answer_text.textContent = "Ваша заявка отправлена, в ближайшее время с Вами свяжется наш специалист.";
		th.trigger("reset");

	  }
	}
	http.onerror = function() {
		mail_answer_title.textContent = "Внимание";
		mail_answer_text.textContent = "Письмо не отправлено";
	}
	http.send(new FormData(f));
	console.log("final")
  }, false);

  let checkboxForm = document.querySelector('.consent-statement-container');
  let listCheckboxForm = document.querySelectorAll('.svg_form');
  checkboxForm?.addEventListener('click', toggleCheckbox);
  let checkboxState = false;
  function toggleCheckbox() {
	if (listCheckboxForm[1].classList.contains('active_svg_form_dis')) {
		listCheckboxForm[1].classList.remove('active_svg_form_dis');
		listCheckboxForm[0].classList.add('active_svg_form_dis');
		checkboxState = true;
	  } else {
		listCheckboxForm[1].classList.add('active_svg_form_dis');
		listCheckboxForm[0].classList.remove('active_svg_form_dis');
		checkboxState = false;
	  }
  }
  let boxBtnBord: NodeListOf<HTMLInputElement> = document.querySelectorAll('.transport-selection-container-button')
  boxBtnBord[0].addEventListener('click', changeBtnState)
  boxBtnBord[1].addEventListener('click', changeBtnState)
  let additionBox: NodeListOf<HTMLElement> = document.querySelectorAll('.contact-info-addition')
  function changeBtnState(this: any) {
	if(this.classList.contains('transport-selection-container-button_active')){
		return
	}
	if(this === boxBtnBord[0]){
		boxBtnBord[1].classList.remove('transport-selection-container-button_active');
		boxBtnBord[0].classList.add('transport-selection-container-button_active');
		boxBtnBord[0].name = boxBtnBord[0].value;
		boxBtnBord[1].name = ''
		additionBox.forEach((el)=> {
				el.style.display = 'block'
		})
	}
	if(this === boxBtnBord[1]){
		boxBtnBord[0].classList.remove('transport-selection-container-button_active');
		boxBtnBord[1].classList.add('transport-selection-container-button_active');
		boxBtnBord[1].name = boxBtnBord[1].value;
		boxBtnBord[0].name = ''
		let inputPerson = document.querySelector('.input-person') as HTMLInputElement;
		inputPerson.name = '';
		let boxSpa: NodeListOf<HTMLInputElement> = document.querySelectorAll('.spa-service-button');
		boxSpa.forEach((el)=> {
			el.name = ''
			if(el.classList.contains('active_spa_btn')){
				el.classList.remove('active_spa_btn');
			}
		})
		additionBox.forEach((el)=> {
			el.style.display = 'none'
	})
	}
	
  }
  let boxSpa: NodeListOf<HTMLInputElement> = document.querySelectorAll('.spa-service-button');
  if(boxBtnBord[0].classList.contains('transport-selection-container-button_active')) {
	boxSpa.forEach((el)=> {
		el.addEventListener('click', function(){
			if(el.classList.contains('active_spa_btn')){
				el.classList.remove('active_spa_btn');
				el.name = ''
			}
			else{
				el.classList.add('active_spa_btn');
				el.name = el.value;
			}
			 
		})
	  })
  }
 /*  validate() */
  const burger = document.querySelector('.burger');
  const popup = document.querySelector('.popup');
  const overlay = document.querySelector('.popup_left');
  burger?.addEventListener('click', openPopup)
  function openPopup(){
	console.log(2)
	popup?.classList.add('popup_active')
  }
  function closePopup(){
	console.log(2)
	popup?.classList.remove('popup_active')
  }
  overlay?.addEventListener('click', closePopup)

  AOS.init({ 
	// Global settings:
	disable: window.innerWidth < 650,  // accepts following values: 'phone', 'tablet', 'mobile', boolean, expression or function
	startEvent: 'DOMContentLoaded', // name of the event dispatched on the document, that AOS should initialize on
	initClassName: 'aos-init', // class applied after initialization
	animatedClassName: 'aos-animate', // class applied on animation
	useClassNames: false, // if true, will add content of `data-aos` as classes on scroll
	disableMutationObserver: false, // disables automatic mutations' detections (advanced)
	debounceDelay: 50, // the delay on debounce used while resizing window (advanced)
	throttleDelay: 99, // the delay on throttle used while scrolling the page (advanced)


	// Settings that can be overridden on per-element basis, by `data-aos-*` attributes:
	offset: 0, // offset (in px) from the original trigger point
	delay: 0, // values from 0 to 3000, with step 50ms
	duration: 500, // values from 0 to 3000, with step 50ms
	easing: 'ease', // default easing for AOS animations
	once: true, // whether animation should happen only once - while scrolling down
	mirror: false, // whether elements should animate out while scrolling past them
	anchorPlacement: 'top-bottom', // defines which position of the element regarding to window should trigger the animation

  });
window.addEventListener('load', AOS.refresh); 
   